<template>
  <div class="editor">
    <header>
      <Heading/>
    </header>

    <main>
      <Sidebar id="sidebar"/>

      <keep-alive v-if="isAnySlideSelected">
        <component :is="activeSlideComponent">
        </component>
      </keep-alive>

      <div
          v-else
          class="help_text"
      >Чтобы начать редактирование, добавьте слайд
      </div>
    </main>
  </div>
</template>

<script>
import Heading from '@/components/Heading.vue';
import Sidebar from "@/components/Sidebar";
import {mapGetters} from "vuex";
import SingleChoice from "@/components/slide-types/SingleChoice";
import Info from "@/components/slide-types/Info";

export default {
  name: 'Editor',
  components: {
    Heading,
    Sidebar,

    SingleChoice,
    Info,
  },
  computed: {
    isAnySlideSelected() {
      return this.activeSlide !== undefined
    },
    ...mapGetters([
      'activeSlide',
      'activeSlideComponent',
    ]),
  },
}
</script>

<style scoped lang="scss">
.editor {
  height: 100%;
  display: flex;
  flex-direction: column;

  header {
  }

  main {
    flex: 1 1 auto;
    overflow: hidden;
    display: flex;

    #sidebar {
      min-width: 290px;
      width: 290px;
    }

    #slide_editor {
      flex: 1 1 auto;
    }

    .help_text {
      flex: 1 1 auto;
      display: flex;
      justify-content: center;
      align-items: center;

      padding: 0 5% 5%;

      font-size: 2rem;
      color: #8c8c8c;
    }
  }
}
</style>
