<template>
  <div class="slide_editor">
    <div class="params_area border-bottom">
      <div class="row px-3">
        <div class="col-3">
            <label
                for="slideTypeSelect"
                class="form-label"
            >
              Тип слайда
            </label>
            <select class="form-select"
                    id="slideTypeSelect"
                    :value="activeSlide.type"
                    @change="setSlideType($event.target.value)"
            >
              <option v-for="type in $store.state.slideTypes"
                      :key="type.id"
                      :value="type.id"
              >{{ type.name }}
              </option>
            </select>
        </div>

        <slot name="params"></slot>
      </div>
    </div>

    <div class="slide_controls_area">
      <div class="slide_area">
        <RatioKeeper
            :ratio="16 / 9"
            :min-height="500"
        >
          <Slide/>
        </RatioKeeper>
      </div>

      <div class="controls_area border-start">
        <button
            @click="addText"
        >
          Text
        </button>

        <button
            disabled
            @click="addImage"
        >
          Image
        </button>

        <button
            disabled
            @click="addYouTube"
        >
          YouTube
        </button>

        <slot name="controls"></slot>

        <div style="flex: 1 1 auto"></div>

        <button
            @click="toggleGrid"
        >Показать/скрыть сетку
        </button>

        <button
            @click="logState"
        >Log vuex state
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import {mapActions, mapGetters, mapMutations} from "vuex";
import {ADD_TEXT_ELEMENT, ADD_IMAGE_ELEMENT, ADD_YOUTUBE_ELEMENT} from "@/store/action-types";
import RatioKeeper from "@/components/utils/RatioKeeper";
import Slide from "@/components/Slide";
import {SET_SLIDE_TYPE, TOGGLE_GRID} from "@/store/mutation-types";

export default {
  name: 'SlideEditor',
  components: {Slide, RatioKeeper},
  props: {},
  computed: {
    ...mapGetters(['activeSlide'])
  },
  methods: {
    ...mapMutations({
      setSlideType: SET_SLIDE_TYPE,
      toggleGrid: TOGGLE_GRID,
    }),
    ...mapActions({
      // addText: ADD_TEXT_ELEMENT,
      addImage: ADD_IMAGE_ELEMENT,
      addYouTube: ADD_YOUTUBE_ELEMENT,
    }),
    addText() {
      this.$store.dispatch(ADD_TEXT_ELEMENT, {x: 5, y: 5, width: 20, height: 10})
    },
    logState() {
      console.log(this.$store.state)
    },
  },
}
</script>

<style lang="scss">
.slide_editor {
  display: flex;
  flex-direction: column;
  overflow: hidden;

  .params_area {
    height: 80px;

    .form-label {
      margin-top: .2rem;
      margin-bottom: .2rem;
    }
  }

  .slide_controls_area {
    flex: 1 1 auto;
    overflow: hidden;
    display: flex;

    .slide_area {
      flex: 1 1 auto;
      overflow: auto;
      background-color: #f1f2f5;

      & > * > div.slide_item {
        box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.2);
      }
    }

    .controls_area {
      width: 100px;
      background-color: #f8f9fb;

      display: flex;
      flex-direction: column;

      button {
        margin: 8px;

        &:first-child {
          margin-top: 16px;
        }
      }
    }
  }
}
</style>
