<template>
  <div
      class="content"
      ref="content"
      :style="styles"
  >
    <slot v-if="ready"></slot>
  </div>
</template>

<script>
export default {
  name: 'RatioKeeper',
  props: {
    ratio: Number,
    minHeight: Number,
  },
  data() {
    return {
      ready: false,
      height: 0,

      paddingX: 20,
      paddingY: 16,
      horizontalScrollPadding: 20,
    }
  },
  computed: {
    contentWidth() {
      return (this.height - this.paddingY * 2) * this.ratio + this.paddingX * 2
    },
    contentHeight() {
      return this.height
    },
    styles() {
      return {
        width: this.contentWidth + 'px',
        height: this.contentHeight + 'px',
        padding: `${this.paddingY}px ${this.paddingX}px`,
      }
    },
  },
  watch: {
    ratio() {
      this.keepAspectRatio()
    },
  },
  methods: {
    changeSelf(transformation) {
      this.ready = false
      transformation().then(() => this.ready = true)
    },
    async keepAspectRatio() {
      let newHeight = this.$refs.content.parentElement.clientHeight - this.horizontalScrollPadding
      if (newHeight > this.minHeight) {
        this.height = newHeight
      } else {
        this.height = this.minHeight
      }
    },
  },
  created() {
    // window.addEventListener('resize', () => this.changeSelf(this.keepAspectRatio))
  },
  mounted() {
    this.changeSelf(this.keepAspectRatio)
  },
  destroyed() {
    // window.removeEventListener('resize', () => this.changeSelf(this.keepAspectRatio))
  },
}
</script>

<style scoped lang="scss">
.content {
  margin: 0 auto;
}
</style>
