<template>
  <div
      class="text_object"
      :class="{inactive: !isActive}"
  >
    <wysiwyg-math
        ref="wysimath"
        :functions="['textStyle', 'fontSize', 'textAlign', 'lists', 'formulaInsert', 'redo']"
        @focusout="onFocusOut"
    ></wysiwyg-math>
  </div>
</template>

<script>
import WysiwygMath from "@/components/wysimath/wysiMath";

export default {
  name: 'TextObject',
  components: {WysiwygMath},
  props: ['html'],
  data() {
    return {
      isActive: false,
    }
  },
  computed: {},
  watch: {},
  methods: {
    focus() {
      this.$refs.wysimath.focus()
      this.isActive = true
      this.$emit('focusin')
    },

    onFocusOut() {
      this.isActive = false
      this.$emit('focusout')
    },
  },
  created() {
  },
  mounted() {
    this.$emit('update-actions', [
      {icon: 'far fa-edit', func: this.focus},
    ])

    window.renderMathInElement(document.body)
  },
}
</script>

<style scoped lang="scss">
.text_object {
  ::v-deep .editor {
    border: 1px solid transparent;

    &:focus {
      box-shadow: none !important;
    }
  }
}
</style>
