<template>
  <div class="sidebar border-end">
    <div
        class="slides"
        ref="slides"
    >
      <div
          v-for="slide in $store.state.slides"
          :key="slide.id"
          class="slide_item"
          :class="{selected: isSlideSelected(slide.id)}"
          @click="setActiveSlide(slide.id)"
      >
        <SlidePreview :slide-id="slide.id"/>
      </div>
    </div>

    <div class="controls" :class="{ 'border-top': hasScroll }">
      <button
          type="button"
          class="btn btn-primary btn-lg"
          @click="addSlide"
      >Добавить слайд
      </button>
    </div>
  </div>
</template>

<script>
import SlidePreview from "@/components/SlidePreview";
import {mapMutations} from "vuex";
import {ADD_SLIDE, SET_ACTIVE_SLIDE} from "@/store/mutation-types";

export default {
  name: 'Sidebar',
  components: {
    SlidePreview,
  },
  props: {},
  data() {
    return {
      mounted: false,
    }
  },
  computed: {
    hasScroll() {
      if (!this.mounted) return false
      else return this.$refs.slides.scrollHeight > this.$refs.slides.clientHeight
    },
  },
  methods: {
    ...mapMutations({
      addSlide: ADD_SLIDE,
      setActiveSlide: SET_ACTIVE_SLIDE,
    }),
    isSlideSelected(id) {
      return id === this.$store.getters.activeSlide.id
    },
  },
  mounted() {
    this.mounted = true
  }
}
</script>

<style scoped lang="scss">
@import "src/assets/styles/mixins";

.sidebar {
  display: flex;
  flex-direction: column;
  overflow-y: hidden;

  .slides {
    @include scrollbar();

    flex: 1 1 auto;
    height: 0;
    overflow-y: auto;
    padding-top: 8px;

    .slide_item {
      padding: .5rem .7rem;

      display: flex;
      justify-content: flex-end;

      cursor: pointer;
    }

    .selected {
      background-color: #daf0fc;
    }
  }

  .controls {
    padding: 1rem;

    button {
      width: 100%;
    }
  }
}
</style>
