import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import {ADD_SLIDE, ADD_SLIDE_ELEMENT} from "@/store/mutation-types";

Vue.config.productionTip = false

store.subscribe((mutation) => {
    if ([ADD_SLIDE].includes(mutation.type)) {
        store.commit('increaseNewSlideId')
    }
    if ([ADD_SLIDE_ELEMENT].includes(mutation.type)) {
        store.commit('increaseNewSlideElementId')
    }
});

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
